.wrapper {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.rickContainer {
  position: relative;
  top: 3em;
  height: 24.815em;
  width: 24.875em;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: repeating-linear-gradient(#fff 0%, #fff 14%, #262626 20%);
  box-shadow: inset 0em 0em 4em black;
  border: 1em solid #141414;
  border-radius: 0.36em;
}
.rickContainer:before {
  content: "";
  position: absolute;
  height: 1em;
  width: 100%;
  bottom: -1em;
  background: #141414;
  z-index: 8000;
}
.rickContainer:after {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  background: radial-gradient(ellipse at 50% 75%, #262626 10%, #262626 10%, transparent 60%);
}

.rick__head {
  position: relative;
}
.rick__ears {
  position: absolute;
  top: 6.55em;
  left: -0.65em;
  right: 0;
  margin: 0 auto;
  height: 3.1em;
  width: 9em;
  background: #E7DFCF;
  border: 0.02rem solid black;
  border-radius: 60%;
  box-shadow: inset -2em -0.5em 0.7em rgba(0, 0, 0, 0.5);
  z-index: 2000;
}
.rick__scalp {
  position: absolute;
  height: 13.118rem;
  width: 7.734rem;
  background: #E7DFCF;
  border-radius: 46%;
  border-top-left-radius: 40%;
  border-top-right-radius: 47%;
  border-bottom-left-radius: 44%;
  border-bottom-right-radius: 44%;
  z-index: 100;
}
.rick__scalp .hair_1 {
  position: absolute;
  border-bottom: 2em solid #8EE1FA;
  border-left: 1em solid transparent;
  border-right: 1em solid transparent;
  left: -0.4em;
  bottom: 1.2em;
  transform: rotate(-130deg);
}
.rick__scalp .hair_2 {
  position: absolute;
  border-bottom: 2em solid #8EE1FA;
  border-left: 1em solid transparent;
  border-right: 1em solid transparent;
  left: -1.3em;
  bottom: 1.98em;
  transform: rotate(-130deg);
}
.rick__scalp .hair_3 {
  position: absolute;
  border-bottom: 4em solid #8EE1FA;
  border-left: 2em solid transparent;
  border-right: 2em solid transparent;
  left: -2.6em;
  bottom: 2.6em;
  transform: rotate(-120deg);
}
.rick__scalp .hair_4 {
  position: absolute;
  border-bottom: 5em solid #8EE1FA;
  border-left: 1em solid transparent;
  border-right: 4em solid transparent;
  left: -2.9em;
  bottom: 5.9em;
  transform: rotate(-60deg);
}
.rick__scalp .hair_5 {
  position: absolute;
  border-bottom: 5em solid #8EE1FA;
  border-left: 1em solid transparent;
  border-right: 4.7em solid transparent;
  left: -3.31em;
  bottom: 9.5em;
  transform: rotate(-48deg);
}
.rick__scalp .hair_6 {
  position: absolute;
  border-bottom: 6em solid #8EE1FA;
  border-left: 1em solid transparent;
  border-right: 3.9em solid transparent;
  left: -0.9em;
  bottom: 11.9em;
  transform: rotate(-26deg);
}
.rick__scalp .hair_7 {
  position: absolute;
  border-bottom: 6em solid #8EE1FA;
  border-left: 1.1em solid transparent;
  border-right: 2.1em solid transparent;
  left: 4.2em;
  bottom: 12.4em;
  transform: rotate(20deg);
}
.rick__scalp .hair_8 {
  position: absolute;
  border-bottom: 6em solid #8EE1FA;
  border-left: 2.1em solid transparent;
  border-right: 2.9em solid transparent;
  left: 6.2em;
  bottom: 9.98em;
  transform: rotate(60deg);
}
.rick__scalp .hair_9 {
  position: absolute;
  border-bottom: 6em solid #8EE1FA;
  border-left: 2.1em solid transparent;
  border-right: 4.4em solid transparent;
  left: 5.2em;
  bottom: 5.98em;
  transform: rotate(90deg);
}
.rick__scalp .hair_10 {
  position: absolute;
  border-bottom: 6em solid #8EE1FA;
  border-left: 2.1em solid transparent;
  border-right: 4.4em solid transparent;
  left: 4.2em;
  bottom: 2.98em;
  transform: rotate(120deg);
}
.rick__scalp .hair_11 {
  position: absolute;
  border-bottom: 6em solid #8EE1FA;
  border-left: 2.1em solid transparent;
  border-right: 4.4em solid transparent;
  left: 3.6em;
  bottom: 1.28em;
  transform: rotate(130deg);
}
.rick__scalp .hair_12 {
  position: absolute;
  border-bottom: 6em solid #8EE1FA;
  border-left: 2.1em solid transparent;
  border-right: 4.4em solid transparent;
  left: 2.36em;
  bottom: 0.8em;
  transform: rotate(150deg);
}
.rick__face {
  position: relative;
  height: 13.118rem;
  width: 7.734rem;
  background: #E7DFCF;
  border-radius: 46%;
  border-top-left-radius: 40%;
  border-top-right-radius: 47%;
  border-bottom-left-radius: 44%;
  border-bottom-right-radius: 44%;
  border: 0.02rem solid black;
  overflow: hidden;
  box-shadow: inset -0.6em -0.5em 0.7em rgba(0, 0, 0, 0.5);
  z-index: 3000;
}
.rick__brow {
  position: absolute;
  top: 2.7em;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 7.5%;
  width: 80%;
  background: #8EE1FA;
  border-bottom-left-radius: 100%;
  border-top-right-radius: 100%;
  border-top-left-radius: 12%;
  border: 0.02em solid #141414;
  z-index: 12;
}
.rick__eyeContainer {
  position: absolute;
  top: 3.3em;
  width: 100%;
  height: auto;
  background: transparent;
  z-index: 8;
}
.rick__eye, .rick__eyeLeft, .rick__eyeRight {
  position: relative;
  height: 3rem;
  width: 40%;
  background: #fff;
  border-radius: 100%;
  border-bottom: 0.02rem solid #141414;
  overflow: hidden;
}
.rick__eye:before, .rick__eyeLeft:before, .rick__eyeRight:before, .rick__eye:after, .rick__eyeLeft:after, .rick__eyeRight:after {
  content: "";
}
.rick__eye:before, .rick__eyeLeft:before, .rick__eyeRight:before {
  position: absolute;
  top: -0.08rem;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 49%;
  width: 100%;
  border-top-right-radius: 100%;
  border-top-left-radius: 100%;
  background: #E7DFCF;
  z-index: 10;
  animation: eye-blink 5s ease-in-out 1s infinite;
}
.rick__eye:after, .rick__eyeLeft:after, .rick__eyeRight:after {
  position: absolute;
  top: 1.3rem;
  left: 0;
  right: 0;
  margin: 0 auto;
  border-radius: 100%;
  height: 16%;
  width: 13%;
  background: black;
  z-index: 9;
}
.rick__eyeRight {
  float: right;
  right: 0.38em;
}
.rick__eyeLeft {
  float: left;
  left: 0.38em;
}
.rick__nose {
  position: absolute;
  top: 6.3em;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 15%;
  width: 13%;
  border-bottom: 0.08rem solid black;
  border-left: 0.03rem solid black;
  border-right: 0.03rem solid black;
  border-bottom-left-radius: 70%;
  border-bottom-right-radius: 60%;
  background: #E7DFCF;
}
.rick__nose:before, .rick__nose:after {
  content: "";
  position: absolute;
  top: -1.56em;
  height: 2.5rem;
  width: 2.2rem;
  border-radius: 100%;
  background: transparent;
}
.rick__nose:before {
  left: 1.35em;
  box-shadow: 0.03em 0.2em 0 -0.1em black;
}
.rick__nose:after {
  right: 1.35em;
  box-shadow: -0.03em 0.2em 0 -0.1em black;
}
.rick__mouth {
  position: absolute;
  height: 40%;
  width: 83%;
  border-top-right-radius: 60%;
  border-top-left-radius: 60%;
  right: 0;
  left: 0;
  margin: 0 auto;
  bottom: -16%;
  background: transparent;
  box-shadow: 0 -0.23em 0 -0.18em black;
}
.rick__mouth:before, .rick__mouth:after {
  content: "";
  position: absolute;
  top: 0;
  height: 1em;
  width: 0.76em;
  background: #E7DFCF;
}
.rick__mouth:before {
  left: 0.8em;
  border-top-left-radius: 100%;
  border-bottom-left-radius: 100%;
  box-shadow: -0.1em 0em black;
}
.rick__mouth:after {
  background-color: transparent;
  right: 0.8em;
  border-top-right-radius: 100%;
  border-bottom-right-radius: 100%;
  box-shadow: 0.1em 0 black, inset -0.5em 9em #e0d8c9;
}
.rick__mouth .drool {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 0.84em;
  width: 1.8em;
  border-top-left-radius: 30%;
  border-top-right-radius: 30%;
  border-bottom-left-radius: 80%;
  border-bottom-right-radius: 100%;
  border-top: 0.02em solid rgba(0, 0, 0, 0.1);
  box-shadow: 0 0.1em 0.15em rgba(0, 0, 0, 0.8);
  background: #8EE1FA;
}
.rick__mouth .drool:before {
  content: "";
  position: absolute;
  bottom: -0.6em;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 1.4em;
  width: 0.84em;
  border-radius: 100%;
  background: #8EE1FA;
  box-shadow: 0 0.21em 0.05em -0.05em rgba(0, 0, 0, 0.8);
}
.rick__mouth .drool:after {
  content: "";
  position: absolute;
  height: 0.28em;
  width: 0.28em;
  top: 1em;
  border-radius: 100%;
  left: -0.23em;
  background: #8EE1FA;
  box-shadow: 0 0 0.01em 0.03em black;
}
.rick__body {
  position: relative;
  top: 0.6em;
  border-bottom: 10.5em solid #fff;
  border-left: 1.5em solid transparent;
  border-right: 1.5em solid transparent;
  height: 0;
  width: 10.45em;
  z-index: 2000;
}
.rick__body:before, .rick__body:after {
  content: "";
  position: absolute;
}
.rick__body:before {
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 2.095em;
  height: 10.4em;
  background: #8EE1FA;
  border: 0.02em solid #141414;
  z-index: -1;
}
.rick__neck {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: -2em;
  height: 2.8em;
  width: 2.3em;
  background: #E7DFCF;
  z-index: -1;
  border-bottom-left-radius: 100%;
  border-bottom-right-radius: 100%;
  box-shadow: inset -0.5em 0.8em 0.7em rgba(0, 0, 0, 0.5);
}

.name__sign {
  position: absolute;
  bottom: 1.4em;
  width: 17em;
  font-family: "Rock Salt", cursive;
  color: #fff;
  font-size: 0.48rem;
  text-align: left;
  letter-spacing: 0.036rem;
  padding: 0.2em 2.8em;
  border-radius: 0.4em;
  background: #606060;
  box-shadow: inset 0em 0em 4em 1em black;
  transform: rotate(-5deg);
  z-index: 9000;
}
.name__sign:before, .name__sign:after {
  content: "";
  position: absolute;
}
.name__sign:before {
  left: 3.7em;
  top: -7.44em;
  width: 0.3em;
  height: 8.9em;
  border-radius: 100%;
  background: black;
  transform: rotate(48deg);
  z-index: -1;
}
.name__sign:after {
  right: 2.4em;
  top: -5.8em;
  width: 0.3em;
  height: 6.3em;
  border-radius: 100%;
  background: black;
  transform: rotate(-30deg);
  z-index: -1;
}

@keyframes eye-blink {
  0%, 20%, 30%, 40% {
    height: 49%;
  }
  50% {
    height: 100%;
  }
  60%, 70%, 80%, 90%, 100% {
    height: 49%;
  }
}